/* Banner4.css */

/* 기존 스타일 */
.banner4 {
    padding: 6rem 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  
  .banner4-content {
    max-width: 1024px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
    /* position: relative; */
  }
  
  .banner4-image {
    background-size: cover;
    background-position: center;
    /* width: 526px;
    height: 735px; */
    height: 590px; /* height: 639px; */
    width: 295px;
    margin-left: 3rem;
    border: 2px solid black; /* Add border to the image */
    border-radius: 10px; /* Make the corners rounded */
  }
  
  .banner4-text {
    max-width: 500px;
  }

  #a-download-button {
    background-color: white; /* f1f3f5 */
    color: #212529;
    text-decoration: none;
    padding: 15px 20px;
    display: inline-block;
    font-size: 18px;
    font-weight: 900;

    border-radius: 5px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    transition: background-color 0.3s ease, color 0.3s ease;
    margin-top: 0.5em;
  }
  #a-download-button:hover {
    background-color: #333;
    color: white;
  }
  
  /* 모바일 화면에서만 스타일 변경 */
  @media (max-width: 1030px) {
    .banner4 {
      padding: 2rem 0;
    }
  
    .banner4-content {
      flex-direction: column;
      text-align: center;
      width: 100%;
      /* position: relative; */
    }
  
    .banner4-image {
      width: 80%;
      height: auto;
      order: 2;
      margin-top: 1rem;
      margin-left: 0;
    }
  
    .banner4-text {
      /* order: 1;
      padding: 1rem;
      width: 100%;
      box-sizing: border-box; */
      /* position: absolute;
      top: 10%;
      left: 50%;
      transform: translate(-50%, -10%); */
      /* background-color: rgba(255, 255, 255, 0.8); 배경 투명도 추가 */
      /* z-index: 1; */
    }
  
    .banner4-text h1,
    .banner4-text p {
      margin: 0;
      font-size: 1.5rem;
      line-height: 1.2;
    }
  
    .banner4-text p {
      font-size: 1rem;
    }
  
    .banner4-text a {
      font-size: 1rem;
      padding: 10px 15px;
    }
  }
  